import Vue from 'vue'
import App from './App.vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faPen, faSave, faBars, faTimes, faReply, faShare, faQuestion } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import VTooltip from 'v-tooltip'

import Router from 'vue-router'
Vue.use(Router)

import PixelMaster from "./components/PixelMaster.vue";
var router = new Router({
	routes: [
		{
			path: '/',
			name: 'Home',
			component: PixelMaster
		}
	]
})


library.add(faPen, faSave, faBars, faTimes, faReply, faShare, faQuestion)

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(VTooltip)

Vue.config.productionTip = false

new Vue({
	router,
  render: h => h(App)
}).$mount('#app')
